import './Tuto.css'
import React, { Component } from 'react'

export default class Tuto extends Component {
    constructor(){
        super();
        this.state={
          users:[],
        }
    }
    componentDidMount(){
        fetch('/c',{
            method:'get',
            mode:'cors',
            headers:{'Content-Type':'application/json'},
        }).then(res=>res.json())
        .then(res=>{
            this.setState({users:res})
        })
    }
    reload(){
        this.props.history.go(0);
    }
    render() {
        return(<div>
        <div>
            <ol className='title'>
                <li>用户id</li>
                <li>用户姓名</li>
                <li>用户密码</li>
                <li style={{width:80}}>删除用户</li>
            </ol>
        </div>
        <div>
            {
                (this.state.users==0)?null:
                this.state.users.map((user)=>{
                    return(
                        <ol className='con'>
                            <li>{user.cid}</li>
                            <li>{user.cname}</li>
                            <li>{user.cps}</li>
                            <li style={{width:80}}><button onClick={()=>{
                                fetch('/cdel',{
                                    method:'post',
                                    mode:'cors',
                                    headers:{'Content-Type':'application/json'},
                                    body:JSON.stringify({"cid":user.cid}),
                                })
                            }
                            }>删除</button></li>
                        </ol>
                    )
                })
            }
        </div>
    </div>)
    }
}