import React from 'react'
import { NavLink } from 'react-router-dom'
import './Header.css'
const Header = () => {
    return (
        <header>
            <nav>
                <NavLink activeClassName='active' exact to='/'>Home</NavLink>
                <NavLink activeClassName='active' to='/tuto'>客户信息</NavLink>
                <NavLink activeClassName='active' to='/blog'>订单列表</NavLink>
            </nav>
        </header>
    ) 
}
export default Header