import React, { Component } from 'react';
import './control.css'

export default class Control extends Component {
    constructor(){
        super();
        this.state={
            inpValue:'',
            todos:[{title:'123',done:false},
            {title:'456',done:true},
            {title:'789',done:true},
            {title:'012',done:false}
        ]
        }
    }

    handleChange=(e)=>{
        this.setState({
            inpValue:e.target.value
        })
    }

    componentDidMount(){
        this.input.focus();
        let todos=localStorage.getItem('todos');
        if(todos){
            this.setState({
                todos:JSON.parse(todos)
            })
        }
    }
    componentDidUpdate(){
        localStorage.setItem(
            'todos',
            JSON.stringify(this.state.todos)
        )
    }
    addTodo=()=>{
        // this.state.todos.push({title:this.state.inpValue})
        // this.setState({
        //     todos:this.state.todos
        // })
        if(this.state.inpValue!=''){
            this.setState({
                todos:[
                    {
                        title:this.state.inpValue,
                        done:false
                    },
                    ...this.state.todos
                ]
            })
            this.setState({inpValue:''});
        }
    }
    delTodo=(idx)=>{
    //    let newTodos=JSON.parse(JSON.stringify(this.state.todos));
    //     newTodos.splice(idx,1);
    //     this.setState({todos:newTodos});

        this.setState({
            todos:this.state.todos.filter((item,index)=>index!=idx)
        });
    }
    toggle=(index)=>{
        let newTodos=JSON.parse(JSON.stringify(this.state.todos));
        newTodos[index].done=!newTodos[index].done;
        this.setState({
            todos:newTodos
        })
    }
    renderTodos=(done)=>{
        return this.state.todos.map((todo,idx)=>{
            if(done==todo.done){
            return  <li>
                    <input type='checkbox' checked={done} 
                    onClick={()=>this.toggle(idx)}/>
                    <span dangerouslySetInnerHTML={{__html:todo.title}}></span>
                    <button onClick={()=>this.delTodo(idx)}>删除</button>
                </li>   
            }
        })
    }

    render() {
        //localStorage.setItem('todos',JSON.stringify(this.state.todos));
        const {todos,inpValue}=this.state;
        let arr1=todos.filter(item=>!item.done);
        return (
            <div className='box'>
                <label htmlFor='inp'>todolist</label>
                <input
                id='inp'
                 ref={inp=>this.input=inp}
                 value={this.state.inpValue}  
                 type='text' 
                 onChange={this.handleChange}/>
                 <button type='submit' onClick={this.addTodo}>添加</button>
                <h2>正在进行{arr1.length}</h2>
                 <ul>
                    {this.renderTodos(false)}
                 </ul>
                 <h2>已经完成</h2>
                <ul style={{color:'red'}}>
                    {this.renderTodos(true)}
                </ul>
            </div>
        )
    }
}