import './Blog.css'
import React, { Component } from 'react'

export default class Blog extends Component {
    constructor(){
        super();
        this.state={
          texts:[],
        }
    }
    componentDidMount(){
        fetch('/massage',{
            method:'get',
            mode:'cors',
            headers:{'Content-Type':'application/json'},
        }).then(res=>res.json())
        .then(res=>{
            this.setState({texts:res})
        })
    }
    reload(){
        this.props.history.go(0);
    }
    render() {
        return(<div>
            <div>
                <ol className='ti'>
                    <li>订单id</li>
                    <li>订单人姓名</li>
                    <li>订单人电话</li>
                    <li>订单人邮箱</li>
                    <li>订单内容</li>
                    <li>订单人id</li>
                    <li>订单状态</li>
                    <li style={{width:80}}>删除订单</li>
                </ol>
            </div>
        <div>
            {
                (this.state.texts==0)?null:
                this.state.texts.map((text)=>{
                    return(
                        <ol className='co'>
                            <li>{text.mid}</li>
                            <li>{text.mname}</li>
                            <li>{text.mtel}</li>
                            <li>{text.mmail}</li>
                            <li>{text.mcon}</li>
                            <li>{text.vcid}</li>
                            <li>{text.mfin}</li>
                            <li style={{width:80}}><button onClick={()=>{
                                fetch('/mdel',{
                                    method:'post',
                                    mode:'cors',
                                    headers:{'Content-Type':'application/json'},
                                    body:JSON.stringify({"mid":text.mid}),
                                }).then(setTimeout(this.reload.bind(this),200))
                            }
                            }>删除</button></li>
                        </ol>
                    )
                })
            }
        </div>
    </div>)
    }
}